import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back
    },
    data () {
        return {
            list:[],
            pageShow: false,
            ListDetails:this.$store.state.preList,
            vipList:this.$store.state.saveVipData.vipList,
            vipTermList:null,
            GoodsTypeNo:this.$route.query.GoodsTypeNo,
        }
    },
    created() {
        this.getFirst(0)
        this.getVip()
    },
    methods: {
        getVip(){
            this.$get('PlatFormAPI/SpecialistHosGroup/GetDoctorServiceInfo',{ //获取Vip数据
                GoodsTypeNo:this.$route.query.GoodsTypeNo
            }).then( m => {
                this.$store.state.saveVipData.vipList = m.Data
                this.vipTermList = m.Data
            })
        },
        getFirst (id) {
            this.$get('AppointmentAPI/Appointment/GetAppointmentTypes',{ //获取列表
                ParentId: id
            }).then( m => {
                this.list = m
                this.pageShow = true
            })
        },
        goVipList(i){
            this.$router.push('/vipClinic?TypeId='+i.TypeId)
        },
        goAllgoods(i){  //跳转
            let spa = i
            spa.GoodsNum = 1
            spa.isVip = 1
            if(this.ListDetails && this.ListDetails.length){
                let t = 0
                this.ListDetails.forEach((v,k) => {
                    if(v.GoodsSource == i.GoodsSource && v.SpecialistGoodsId == i.SpecialistGoodsId && v.SpecialistHosGroupId == i.SpecialistHosGroupId && v.GoodsId == i.GoodsId){
                        this.ListDetails.splice(k, 1, spa)
                        t = 1
                    }
                })
                if(t == 0){
                    this.ListDetails.push(spa)
                }
            }else{
                this.ListDetails.push(spa)
            }
            //this.$store.dispatch('pushIsFresh', true)
            this.$store.dispatch('pushnavNum',1)
            this.$store.dispatch('pushpreList',this.ListDetails)
            this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
            this.$router.push('/allGoods')
        }
    },
}



